import { Picker, PickerItem, PickerProps, Text, Image } from "@react-spectrum/s2";
import {style} from '@react-spectrum/s2/style' with {type: 'macro'};
import React from 'react';
import {Team, Teams} from '../types';

export function TeamPicker(props: PickerProps<Team>) {
  return (
    <Picker {...props}>
      {
        item => (
          <PickerItem id={item.id}>
            <Image src={item.logo[0]} slot="icon" styles={style({size: '[20px]', alignSelf: 'center', justifySelf: 'center'})} />
            <Text slot="label">{item.name}</Text>
          </PickerItem>
        )
      }
    </Picker>
  )
}