import data from '../data/data.json';
import React from "react";
import {ComparisonTable} from "./ComparisonTable";
import { WeekComparison } from './WeekComparison';
import {Projections} from './Projections';
import {Heading, Tabs, Tab, TabList, TabPanel} from '@react-spectrum/s2';
import { PerformanceHistory } from './PerformanceHistory';
import {style} from '@react-spectrum/s2/style' with {type: 'macro'};

export function App() {
  return (
    <div className={style({fontFamily: 'sans'})}>
      <Heading>GCNS Analytics</Heading>
      <Tabs defaultSelectedKey="week">
        <TabList>
          <Tab id="week">Week comparison</Tab>
          <Tab id="perf">Performance history</Tab>
          <Tab id="proj">Projections</Tab>
        </TabList>
        <TabPanel id="week">
         <WeekComparison teams={data.teams} stats={data.stats} />
        </TabPanel>
        <TabPanel id="perf">
         <PerformanceHistory teams={data.teams} stats={data.stats} />
        </TabPanel>
        <TabPanel id="proj">
         <Projections teams={data.teams} stats={data.stats} />
        </TabPanel>
      </Tabs>
      {/* <ComparisonTable teams={data.teams} stats={data.stats}/> */}
    </div>
  )
}