import projectionData from '../data/projections.json';
import data from '../data/data.json';
import {ActionButton, Image,  DialogTrigger, Dialog} from '@react-spectrum/s2';
import {style} from '@react-spectrum/s2/style' with {type: 'macro'};
import React, {useMemo, useState} from 'react';
import { NumberField, Input, Group, ToggleButton } from 'react-aria-components';
import { getDifference, isWinner, statMap } from '../util.js';
import Reset from '@react-spectrum/s2/icons/Revert';
import Add from '@react-spectrum/s2/icons/Add';
import type {CompareCategory} from '../types.js';


function filterPosition(position) {
  return !['G', 'F', 'Util', 'IL'].includes(position);
}

export function Projections(props) {
  const {teams, stats} = props;
  const {dateRange, projections, rosters, schedule} = projectionData;
  const {} = useState();

  // sort our team first
  const [team1, team2] = Object.keys(rosters).sort((a, b) => a === '5' ? -1 : 1 );
  // TODO add current week to projections script
  const weeks = Object.keys(stats);
  const currentWeek = weeks[weeks.length - 1];

  console.log('stats', stats)
  return (
    <div>
      <Scoreboard 
        team1={teams[team1].name} 
        team2={teams[team2].name} 
        t1Stats={stats[currentWeek].stats[team1]}
        t2Stats={stats[currentWeek].stats[team2]}
        t1Projections={{}}
        t2Projections={{}}
      />
      {renderRoster(rosters[team1], projections, schedule, dateRange)}
      {renderRoster(rosters[team2], projections, schedule, dateRange)}
    </div>
  )
}

const grid = style({
  display: 'grid', 
  rowGap: 8, 
  columnGap: 4, 
  alignItems: 'center', 
  gridTemplateColumns: [
    '23px', 
    '200px', 
    'auto', 
    '100px', 
    '100px', 
    '60px', 
    '60px', 
    '60px', 
    '60px', 
    '60px', 
    '60px', 
    '60px', 
    '60px'
  ]
});

const teamToggle = style({
  width: '[75px]',
  display: 'flex', 
  alignItems: 'center',
  backgroundColor: {
    default: 'gray-100',
  },
  opacity: {
    default: 0.3,
    isSelected: 1
  },
  borderRadius: 'sm',
  borderWidth: 0,
  borderStyle: 'solid',
  borderColor: {
    default: 'gray-25',
    isSelected: 'blue-900',
  },
  padding: 4,
  outlineStyle: 'none'
});

function renderRoster(roster, projections, schedule, dateRange) {
  const dateStyle = {width: `${100 / dateRange.length}%`};
  return (
    <div className={grid}>
      <span></span>
      <span>Player</span>
      <div className={style({width: 'full', display: 'flex'})}>
        {dateRange.map(date => <div className={style({textAlign: 'center'})} style={dateStyle}>{date.replace(/^\d{4}-/, '')}</div>)}
      </div>
      {
        Object.keys(statMap).map(cat => <div className={style({display: 'flex', justifyContent: 'center'})}>{cat}</div>)
      }
      <span></span>

      {roster
        .sort((a, b) => a.playerName > b.playerName ? 1 : -1)
        .sort((a, b) => a.playerStatus === 'INJ' ? 1 : -1)
        .map((player) => {
        const teamSchedule = schedule[player.playerTeamAbbr];
        const status = player.playerStatus && <PlayerStatus status={player.playerStatus} />;
        // console.log(player)
        // console.log(schedule[player.playerTeamAbbr])

        return (
          <>
            <span>
              <Image 
                src={player.playerAvatar} 
                alt={player.playerName}
                styles={style({width: '[23px]', height: '[30px]'})} />
              </span>
            <span className={style({display: 'flex', flexDirection: 'column'})}>
              <span>{player.playerName}</span>
              <span className={style({display: 'flex', alignItems: 'center', gap: 8,fontSize: '[12px]'})}>
                <span>{player.playerTeamAbbr} - {player.eligiblePositions.filter(filterPosition).join(', ')}</span>
                {status}
              </span>
            </span>
            <div className={style({width: 'full', display: 'flex'})}>
              {dateRange.map(date => {
                if (!teamSchedule[date]) return <div style={dateStyle}></div>;

                const {opponentLogo, opponent, isHomeTeam} = teamSchedule[date];
                return  <div className={style({display: 'flex', justifyContent: 'center'})} style={dateStyle}>
                  <ToggleButton className={teamToggle}>
                    {!isHomeTeam ? "@" : ""}
                    <Image src={opponentLogo} alt={opponent} styles={style({size: 20, paddingEnd: 4})}/>
                    {opponent}
                  </ToggleButton>
                </div>;
              })}
            </div>
            {renderPlayerProjections(player.playerName, projections, Object.keys(teamSchedule).length)}
          </>
        )
      })}
    </div>
  )
}

function renderPlayerProjections(playerName, projections, gameCount) {
  const normalizedName = playerName.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
  // if (!projections[normalizedName]) {
  //   console.log('no', normalizedName)
  //   return (
  //     <>
  //        {Object.keys(statMap).map(cat => <span>-</span>)}
  //     </>
  //   )
  // }
  return <ProjectionsRow projections={projections[normalizedName]} />
  return (
    <>
      {Object.entries(statMap).map(([cat, {label}]) => { 
        const defaultValue = projections[normalizedName] ? projections[normalizedName][cat] * gameCount : 0;
        return (
        <div>
          {/* <div>{projections[normalizedName][cat]}</div> */}
          <CategoryInput name={label} defaultValue={defaultValue} />
        </div>
      )})}
    </>
  )
}

function ProjectionsRow(props) {
  const {projections} = props;
  const defaultValues = useMemo(() => 
    projections ?? 
      Object.keys(statMap).reduce((acc, cat) => {
        acc[cat] = cat.includes('%') ? [0, 0] : 0;
        return acc;
       }, {})
    , []);
  const [values, setValues] = useState<Record<CompareCategory, number[] | number>>(defaultValues);

  const onReset = () => setValues({...defaultValues});

  
  return (
    <>
      {Object.entries(statMap).map(([cat, {label}]) => { 
          const onChange = (newValue) => {
            setValues(values => {
              const newValues = JSON.parse(JSON.stringify(values));
              newValues[cat] = newValue;
              return newValues;
            });
          };
          return (
            <div>
          {/* TODO update style if value changed */}
              <CategoryInput category={cat} name={label} value={values[cat]} onChange={onChange} />
            </div>
        )})}
        <div>
          {/* TODO disable if row has not been changed */}
          <ResetButton onPress={onReset} /> 
        </div>
    </>
  )
}

const catInput = style({
  width: {
    default: 28,
    isPctCat: 28
  }, 
  textAlign: 'center',
  borderRadius: 'sm', 
  borderColor: 'gray-200', 
  borderWidth: 1, 
  borderStyle: 'solid', 
  padding: 4
});

function CategoryInput(props) {
  const {category, defaultValue, value, name, onChange} = props;
  const isPctCat = ['FG%', 'FT%'].includes(category);
  const [showActions, setShowActions] = useState(false);

  return (
    <div className={style({position: 'relative'})}>
      <div className={style({display: 'flex', gap: 4, justifyContent: 'center'})}>
        <NumberField aria-label={name}
            value={isPctCat ? value[0] : value} 
            onFocus={() => setShowActions(true)}
            onBlur={() => setShowActions(false)}
            onChange={onChange}>
          <Input 
            className={catInput({isPctCat})}  />
        </NumberField>
        {isPctCat && <NumberField aria-label={name + '2'}
            value={value[1]} 
            onFocus={() => setShowActions(true)}
            onBlur={() => setShowActions(false)}
            onChange={onChange}>
          <Input 
            className={catInput({isPctCat})}  />
        </NumberField>}
        {/* <ActionButton size="XS" isQuiet onPress={onReset} isDisabled={value === defaultValue}>
          <Reset />
        </ActionButton> */}
      </div>
      {isPctCat && <div className={style({fontSize: '[11px]', textAlign: 'center'})}>{value[1] === 0 ? '-' : (value[0] / value[1]).toPrecision(3)}</div>}
      {showActions && (
        <div className={style({position: 'absolute', bottom: '[-100%]'})}>
          <ResetButton />
        </div>
      )}
    </div>
  );
}

function ResetButton(props) {
  return (
    <ActionButton size="XS" isQuiet {...props}>
      <Reset />
    </ActionButton>
  )
}

function PlayerStatus({status}) {
  return (
    <span className={style({display: 'flex', alignItems: 'center', gap: 4})}>

      <div className={style({display: 'flex',padding: 0, margin: 0, lineHeight: '[0.8]', justifyContent: 'center', fontWeight: 'bold', color: 'gray-25', backgroundColor: 'red-900', borderRadius: 'sm', size: '[12px]'})}>
        +
      </div>
      <span className={style({color: 'red-900', fontWeight: 'bold'})}>{status}</span>
    </span>

    // <span className={style({color: 'red-800'})}> - {status}</span>
  )
}

