  //     const [t2m, t2a] = t1Stats[maKey].split('/');
  //     team1Val = (parseInt(t1m, 10) + t1Projections[generalKey][0]) / (parseInt(t1a, 10) + t1Projections[generalKey][1]);
  //     team2Val = (parseInt(t2m, 10) + t2Projections[generalKey][0]) / (parseInt(t2a, 10) + t2Projections[generalKey][1]);
  //   } else {
