function renderStatCells(teamStats) {
  return (
    <>
      {Object.entries(statMap).map(([statKey, stat]) => {
        if (statKey === 'FG%') {
          return <td>{teamStats['FG%']} ({teamStats['FGM/A']})</td>;
        } else if (statKey === 'FT%') {
          return <td>{teamStats['FT%']} ({teamStats['FTM/A']})</td>;
        }

        return <td>{teamStats[statKey]}</td>
      })}
    </>
  );
}