interface PerformanceHistoryProps {
  stats: Stats;
  teams: Teams;
}

export function PerformanceHistory(props: PerformanceHistoryProps) {
  const {stats, teams} = props;

  const [stat, setStat] = useState<CompareCategory>('FG%');
  const [chartType, setChartType] = useState('boxPlot');
  const [selectedTeams, setSelectedTeams] = useState(['5']); // default to our team

  const labels = Object.keys(stats).map(week => `Week ${week}`);
  return (
    <div className={style({display: 'flex', flexDirection: 'column', gap: 16, marginTop: 8})}>
      <div className={style({display: 'flex', gap: 16})}>
        <Toolbar className={style({display: 'flex', gap: 2})}>
          <ToggleButton
            isQuiet
            isSelected={chartType === 'boxPlot'}
            onPress={() => setChartType('boxPlot')}>
            Box plot
          </ToggleButton>
          <ToggleButton
            isQuiet
            isSelected={chartType === 'line'}
            onPress={() => setChartType('line')}>
            Line
          </ToggleButton>
        </Toolbar>
        <Toolbar className={style({display: 'flex', gap: 2})}>
          {Object.keys(statMap).map(label => 
            <ToggleButton 
              isQuiet
              isSelected={stat === label} 
              onPress={() => setStat(label)}>{label}</ToggleButton>
          )}
        </Toolbar>
        <DialogTrigger type="popover" hideArrow>
          <ActionButton>Team data (1)</ActionButton>
          <Dialog>
            <Heading>Teams</Heading>
            <Content>
              checkbox group here
            </Content>
          </Dialog>
        </DialogTrigger>
      </div>
      <div>
        {chartType === 'boxPlot' && (
          <BoxPlot stat={stat} labels={labels} stats={stats} selectedTeams={selectedTeams} teams={teams} />
        )}
        {chartType === 'line' && <LineGraph stat={stat} labels={labels} stats={stats} teams={teams} />}
      </div>
    </div>
  );
}

const COLORS = ['red', 'orange', 'gold', 'green', 'blue', 'purple'];

interface BoxPlotProps {
  labels: string[];
  stats: Stats;
  stat: CompareCategory;
  selectedTeams: string[];
  teams: Teams;
}

function BoxPlot(props: BoxPlotProps) {
  const { labels, stats, stat, selectedTeams, teams } = props;

  const [boxPlotData, lineData] = React.useMemo(() => {
    const boxData = [],
      lineData = [];
    Object.entries(stats).forEach(([, teamsObj], i) => {
      Object.entries(teamsObj.stats).forEach(([teamKey, teamObj]) => {
        const selectedIndex = selectedTeams.findIndex(selectedKey => selectedKey === teamKey);

        if (!boxData[i]) {
          boxData[i] = [];
        }

        boxData[i].push(teamObj[stat]);
        if (selectedIndex > -1) {
          if (!lineData[selectedIndex]) {
            lineData[selectedIndex] = [];
          }
          lineData[selectedIndex][i] = teamObj[stat];
        }
      });
    });

    return [boxData, lineData];
  }, [stats, stat, selectedTeams]);

  console.log('linedata', lineData);
  const lineDatasets = lineData.map((data, i) => {
    console.log('data', data);
    const currentTeam = teams[selectedTeams[i]];
    return {
      label: currentTeam.name,
      type: 'line',
      data,
      borderColor: COLORS[i],
      backgroundColor: COLORS[i],
      pointBorderColor: 'black'
    };
  });
  const data = {
    labels,
    datasets: [
      ...lineDatasets,
      {
        label: 'Box',
        type: 'boxplot',
        data: boxPlotData,
        borderColor: BLUE,
        backgroundColor: '#a4cef5',
        itemStyle: 'circle',
        itemBackgroundColor: GOLD,
        outlierBackgroundColor: 'gray'
      }
    ]
  };

  let options = {
    responsive: true,
    plugins: {
      legend: { position: 'bottom' },
      title: { display: true, text: 'League performance' }
    },
    scales: { y: { min: 0 } }
  };

  // set minimum for percentages (FG/FT)
  if (stat === 'FG%') {
    options.scales.y.min = 0.3;
  } else if (stat === 'FT%') {
    options.scales.y.min = 0.6;
  }
  return <Chart options={options} data={data} />;
}

interface LineGraphProps {
  labels: string[];
  stats: Stats;
  stat: CompareCategory;
  teams: Teams;
}

function LineGraph(props: LineGraphProps) {
  const { labels, stats, stat, teams } = props;

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'bottom'
      },
      title: {
        display: true,
        text: 'Team performance'
      }
    }
  };

  let datasets = useMemo(() => {
    let data = [];
    Object.entries(stats).forEach(([, teamsObj], week) => {
      Object.entries(teamsObj.stats).forEach(([teamKey, teamObj], i) => {
        // const { index, myTeam, name, stats } = teamObj;
        const myTeam = teamKey === '5'; // TODO 

        if (!data[i]) {
          // initialize each team
          data[i] = {
            label: teams[teamKey].name,
            data: [],
            borderColor: myTeam ? GOLD : BLUE,
            backgroundColor: myTeam ? GOLD : BLUE,
            order: myTeam ? 0 : 10,
            borderWidth: myTeam ? 3 : 1,
            hoverBackgroundColor: 'white'
          };
        }

        data[i].data[week] = teamObj[stat];
      });
    });
    return data;
  }, [stat, stats]);
  console.log('line data', datasets);

  const data = {
    labels,
    datasets
  };

  return <Line options={options} data={data} />;
}
