import React, { useMemo, useRef, useState } from "react";
import { CompareCategory, Stats, Teams } from "../types";
import {style} from '@react-spectrum/s2/style' with {type: 'macro'};
import {ToggleButton, ActionButton, Divider} from '@react-spectrum/s2';
import { TeamPicker } from "./TeamPicker";
import { Line } from 'react-chartjs-2';
import { statMap } from "../util";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';
import { BoxPlotChart, BoxPlotController, BoxAndWiskers } from '@sgratzl/chartjs-chart-boxplot';


interface WeekComparisonProps {
  stats: Stats;
  teams: Teams;
}


ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  BoxPlotController,
  BoxAndWiskers
);

export function WeekComparison(props: WeekComparisonProps) {
  const {stats, teams} = props;
  const [team1, setTeam1] = useState<string>('5'); // default to our team
  const [team2, setTeam2] = useState<string>();
  const [dataDiff, setDataDiff] = useState<'pct' | 'total'>('pct');
  const lineChart = useRef();

  const hasData = !!(team1 && team2);
  let labels = Object.keys(stats).map(week => `Week ${week}`);

  let [datasets, records, diff] = useMemo(() => {
    let data = [];
    let record: Record<string, [number, number, number]> = [];
    let differential = [];
    if (!hasData) {
      return [];
    }
    Object.entries(statMap).forEach(([statKey, statObj], i) => {
      differential[i] = [];
      const statData = Object.entries(stats).map(([_, teamStats], weekIndex) => {
        const t1Stat = teamStats.stats[team1][statKey];
        const t2Stat = teamStats.stats[team2][statKey];
        let diff = t1Stat - t2Stat;
        if (Number.isNaN(diff)) {
          diff = 0;
        }

        differential[i][weekIndex] = t2Stat === 0 || Number.isNaN(t2Stat) || Number.isNaN(t1Stat) 
          ? 0 
          : +(((t1Stat - t2Stat) / t2Stat) * 100).toFixed(2);

        if (!record[weekIndex]) {
          record[weekIndex] = [0, 0, 0];
        }

        if (diff === 0 || Number.isNaN(diff)) {
          record[weekIndex][2]++; // tie
        } else {
          diff * (statObj.reverse ? -1 : 1) > 0 ? record[weekIndex][0]++ : record[weekIndex][1]++;
        }

        return diff;
      });

      data[i] = {
        label: statKey,
        data: dataDiff === 'pct' ? differential[i] : statData,
        borderColor: statObj.color,
        pointBorderColor: 'black',
        backgroundColor: statObj.color,
        yAxisID: dataDiff === 'total' && statKey.includes('%') ? 'y1' : 'y',
        // https://www.chartjs.org/docs/latest/configuration/elements.html#point-styles
        pointStyle: point => {
          if (point.raw === 0) {
            return 'rectRounded';
          }
          return point.raw * (statObj.reverse ? -1 : 1) > 0 ? 'circle' : 'triangle';
        },
        radius: 6
      }
    });
    
    return [data, record, differential];
  }, [stats, team1, team2, dataDiff]);

  const options = {
    responsive: true,
    // animation: false,
    plugins: {
      legend: {
        position: 'right'
      },
      title: {
        display: true,
        text: 'Head to head comparison'
      },
      // https://www.chartjs.org/docs/latest/configuration/tooltip.html
      tooltip: {
        callbacks: {
          label: context => {
            if (dataDiff === 'pct') {
              return `${context.dataset.label}: ${context.formattedValue}%`;
            }
            return `${context.dataset.label}: ${context.formattedValue} (${
              diff[context.datasetIndex][context.dataIndex]
            }%)`;
          }
        }
      }
    },
    scales: {
      x: {
        ticks: {
          callback: (value, index, ticks) => {
            return [
              labels[index],
              `(${records[index][0]}-${records[index][1]}` +
                (records[index][2] > 0 ? `-${records[index][2]}` : '') +
                ')'
            ];
          }
        }
      },
      y: {
        type: 'linear',
        display: true,
        position: 'left',
        title: { display: true, text: 'Counting' },
        ticks: {
          font: {
            // https://www.chartjs.org/docs/latest/general/fonts.html
            size: tick => (tick.tick?.value === 0 ? '20px' : '12px'),
            weight: tick => (tick.tick?.value === 0 ? 'bold' : 'normal')
          }
        },
        grid: {
          lineWidth: tick => {
            return tick.tick.value === 0 ? 2 : 1;
          },
          color: tick => {
            return tick.tick.value === 0 ? 'black' : '#c0c0c0';
          },
          drawOnChartArea: ctx => {
            return true;
          }
        }
      },
      y1: {
        type: 'linear',
        display: dataDiff === 'total',
        position: 'right',
        title: { display: true, text: 'Percentage' },
        ticks: {
          font: {
            size: tick => (tick.tick?.value === 0 ? '20px' : '12px'),
            weight: tick => (tick.tick?.value === 0 ? 'bold' : 'normal')
          }
        },
        grid: {
          drawOnChartArea: ctx => {
            return false;
          }
        }
      }
    },
    stacked: false
  };
  
  // const onDataAction = key => {
  //   if (!lineChart.current) {
  //     return;
  //   }
  //   if (key === 'hide' || key === 'show') {
  //     for (let i = 0; i < datasets.length; i++) {
  //       lineChart.current.setDatasetVisibility(i, key === 'show');
  //     }
  //   } else if (key === 'pct' || key === 'cnt') {
  //     for (let i = 0; i < datasets.length; i++) {
  //       const stat = Object.values(STAT_MAP)[i];
  //       lineChart.current.setDatasetVisibility(
  //         i,
  //         (stat.short.includes('%') && key === 'pct') || (key === 'cnt' && !stat.short.includes('%'))
  //       );
  //     }
  //   }
  //   lineChart.current.update();
  // };
  console.log('labels', labels)
  console.log('datasets', datasets)
  return (
    <div className={style({display: 'flex', flexDirection: 'column', gap: 16, fontFamily: 'sans'})}>
      <div className={style({display: 'flex', gap: 16, alignItems: 'center', justifyContent: 'center'})}>
    
        <TeamPicker label="Team 1" items={Object.values(teams)} selectedKey={team1} onSelectionChange={setTeam1} />
        <span>vs.</span>
        <TeamPicker label="Team 2" items={Object.values(teams)} selectedKey={team2} onSelectionChange={setTeam2} />
      </div>
      <Divider size="S" />
      {hasData ? (
        <>
          {/* <div className={style({display: 'flex', gap: 16})}>
            <ActionGroup
              selectionMode="single"
              density="compact"
              selectedKeys={[dataDiff]}
              onSelectionChange={sel => setDataDiff(Array.from(sel)[0])}
            >
              <Item key="pct">Percentage difference</Item>
              <Item key="total">Total difference</Item>
            </ActionGroup>
            <Divider orientation="vertical" size="M" />
            <ActionGroup onAction={onDataAction}>
              <Item key="show">Show all</Item>
              <Item key="hide">Hide all</Item>
              <Item key="pct">Percentages only</Item>
              <Item key="cnt">Counting stats only</Item>
            </ActionGroup>
          </div> */}
          <Line ref={lineChart} options={options} data={{ labels, datasets }} />
        </>
      ) : (
        <div className={style({height: '[500px]', display: 'flex', alignItems: 'center', justifyContent: 'center'})}>
          Select 2 teams
        </div>
      )}
    </div>
  );
}